import React, {useState} from 'react';
import { Link, Route } from 'react-router-dom';
import About from './About';
import Contact from './Contact';
import Logos from './Logos';
import Websites from './Websites';


function Navbar() {

    // Declares state variables using React Hooks 
    const [ pic, setPic ] = useState('true');

    return (
        <nav className='navbar-container'>
            <ul className='navbar-ul'>
                <li><Link to='/' onClick={() => setPic('false')} className='nav-link'>Home</Link></li>
                <li><Link to='/about' className='nav-link'>About</Link></li>
                <li><Link to='/logos' className='nav-link'>Logos</Link></li>
                <li><Link to='/websites' className='nav-link'>Websites</Link></li>
                <li><Link to='/contact' className='nav-link'>Contact</Link></li>
            </ul>
            
            {/* Mobile Menu to display with media querie */}
            <a className="mobile-nav">X</a>

            <Route path="/about" component={About} exact />
            <Route path="/logos" component={Logos} exact />
            <Route path="/websites" component={Websites} exact />
            <Route path="/contact" component={Contact} exact />
        </nav>
    )
}

export default Navbar;